label > span {
  margin: 0.5em;
}

label > input {
  margin: 0.25em;
  border-radius: 0.5em;
  background-color: #eeeeee;
  border-style: none;
  height: 1em;
  padding: 0.6em;
  outline: none;
  text-align: left;
  box-shadow: none;
  transition: box-shadow 500ms;
}

label > input:hover {
  border-radius: 0.5em;
  box-shadow: inset rgba(100, 100, 111, 0.2) 0px 0px 10px 0px;
  /* box-shadow: rgba(100, 100, 111, 0.3) 0px 7px 29px 0px; */
}

label > input:focus {
  box-shadow: inset rgba(100, 100, 111, 0.2) 0px 0px 20px 0px;
}

.searchBar {
  transition: 500ms;
  margin-top: 2rem;
}
